import React, { Component } from 'react';
import 'react-widgets/dist/css/react-widgets.css';
import {
  Button,
  Card,
  CardBody,
  CardHeader,
  Col,
  FormGroup,
  Input,
  Label,
  Row,
} from 'reactstrap';
import {Combobox} from 'react-widgets';
import axios from 'axios';
import {BootstrapTable, TableHeaderColumn,TableDataColumn} from 'react-bootstrap-table';
import Grid from '@material-ui/core/Grid';
import DateFnsUtils from '@date-io/date-fns';
import {
  MuiPickersUtilsProvider,
  KeyboardDatePicker,
} from '@material-ui/pickers';
import { saveAs } from 'file-saver';
class PettyCashBookForm extends Component {
    
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.toggleFade = this.toggleFade.bind(this);
    this.state = {
      collapse: true,
      fadeIn: true,
      timeout: 300,
      selectedDate: new Date(),
      pettyCash:[],
      payTotal:0
    };
    this.getPettyCash=this.getPettyCash.bind(this);
  }

  componentDidMount(){
    this.getPettyCash();
  }
  handleDateChange = (date) =>{
    this.setState({
      selectedDate:date,
      
    },()=>{
      this.getPettyCash();
      this.getTotal();
    })  
  };
  toggle() {
    this.setState({ collapse: !this.state.collapse });
  }

  toggleFade() {
    this.setState((prevState) => { return { fadeIn: !prevState }});
  }
    //fetch lorries from database
     getPettyCash= async ()=>{
      var date = this.state.selectedDate.getFullYear() + '-' + (this.state.selectedDate.getMonth() + 1) + '-' + this.state.selectedDate.getDate();
      axios.post('/getpettycashbook',{
          date:date
      }).then(res=>{
        this.setState({
          pettyCash:res.data,
          payTotal:0
        },()=>{
          this.getTotal();
        })
      })
    }
    getTotal(){
      var total=this.state.payTotal;
      if(this.state.pettyCash.length>0){
        for(var i=0;i<this.state.pettyCash.length;i++){
          total+=this.state.pettyCash[i].paymentsAmount;
        }
        this.setState({
          payTotal:total
        })
      }
      else{
        return 0;
      }
    }
    onGetPettyCashBook= async()=>{
        var date = this.state.selectedDate.getFullYear() + '-' + (this.state.selectedDate.getMonth() + 1) + '-' + this.state.selectedDate.getDate();
       await axios.post(`/getpettycashbookRp`, {
          pettyCash : this.state.pettyCash,
          date : date
         })
         .then(() => axios.get('/fetch-pdf', { responseType: 'blob' }))
         .then((res) => {
           const pdfBlob = new Blob([res.data], { type: 'application/pdf' });
    
           saveAs(pdfBlob, `PettyCashBook-${date}.pdf`);
         })
        }
        
    // price formatter
    priceFormat(cell){
      return cell.toFixed(2);
    } 

  render() {
    return (
      <div className="animated fadeIn">
        <Row>
          <Col xs="12" sm="12">
            <Card>
              <CardHeader>
                <strong>Petty Chash Book</strong>
                <FormGroup row>
                 
                  <MuiPickersUtilsProvider utils={DateFnsUtils}>
                    <Grid container justify="space-around">
                      <Col>
                      <KeyboardDatePicker
                        margin="normal"
                        id="date-picker-dialog"
                        label="Date"
                        format="yyyy-MM-dd"
                        value={this.state.selectedDate}
                        onChange={this.handleDateChange}
                        KeyboardButtonProps={{
                        'aria-label': 'change date',
                        }}
                      />
                      </Col>
                  </Grid>
                  </MuiPickersUtilsProvider>
                  </FormGroup>
              </CardHeader>
              <CardBody style={{backgroundColor:"#d9d9d9"}}>
              <div>
               <BootstrapTable data={this.state.pettyCash} height='400' scrollTop={'Bottom'} search={true}
                  bodyStyle={ { border: 'green 1px solid' }}
                  containerStyle={ { border: '#FFBB73 2.5px solid' } } exportCSV={ true }>
                  <TableHeaderColumn width="70" tdStyle={ { whiteSpace: 'normal' } } isKey dataField="pettyId"> No</TableHeaderColumn>
                  <TableHeaderColumn width="120" tdStyle={ { whiteSpace: 'normal' } } dataField="vehicle_vehicleNumber">Vehicle</TableHeaderColumn>
                  <TableHeaderColumn width="140" tdStyle={ { whiteSpace: 'normal' } } dataField="paymentTypeType">Type</TableHeaderColumn>
                  <TableHeaderColumn width="200" tdStyle={ { whiteSpace: 'normal' } } dataField="paymentsDescription">Description</TableHeaderColumn>
                  <TableHeaderColumn width="110" tdStyle={ { whiteSpace: 'normal' } } dataField="paymentsAmount"
                  dataFormat={this.priceFormat.bind(this)}
                  tdStyle={{'textAlign':'right'}}>Amount</TableHeaderColumn>
               </BootstrapTable>
            </div>
                <FormGroup row>
                  <Col md="8">
                        <table>
                          <tr>
                            <td><h5>Total Payments : Rs. </h5> </td>
                            <td><h5>{this.state.payTotal.toFixed(2)}</h5></td>
                          </tr>
                        </table>
                  </Col>
                  <Col></Col>
                  <Col>
                        <Button color="info" onClick={()=>{this.onGetPettyCashBook()}}>Pettycash Book</Button>
                  </Col>
                </FormGroup>
              </CardBody>
            </Card>
          </Col>
        </Row>
       
        
      </div>
    );
  }
}

export default PettyCashBookForm;
